.hero-blog {
  margin-bottom: 50px;
  &__inner {
    overflow: hidden;
    border-radius: 20px;
    background: #f8f8f8;
    padding: 30px 40px;
    position: relative;
  }

  &__image {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
    max-width: 380px;

    @media (max-width: 1140px) {
      display: none;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
    margin-bottom: 14px;
    margin-top: 40px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 136%;
    letter-spacing: -0.02em;
    color: var(--gray);
    margin: 0;
    margin-bottom: 28px;
  }

  &__tags {
    display: flex;
    gap: 15px;

    .tag {
      border-radius: 100px;
      padding: 10px 22px;
      background: var(--white);
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--tekst);

      &.active {
        background: var(--accent);
        color: var(--white);
      }
    }
  }
}

.programs {
  margin-bottom: 100px;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 14px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__left {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 14px;
  }

  &__item {
    border-radius: clamp(10px, 2vw, 20px);
    display: flex;
    flex-direction: column;
    background: #f7f7f7;
    padding: 30px;

    @media (max-width: 1200px) {
      padding: 20px 10px;
    }
    &:nth-child(2) {
      background: var(--blue);
    }
    &:nth-child(3) {
      background: var(--blue);
    }
    &-title {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: clamp(16px, 1.5vw, 18px);
      line-height: 94%;
      letter-spacing: -0.02em;
      color: var(--gray);
      padding-bottom: 102px;
    }

    &-date {
      border-radius: 100px;
      padding: 5px 9px;
      background: var(--white);
      display: flex;
      align-items: center;
      gap: 16px;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--accent);
      max-width: fit-content;
      .icon {
        fill: var(--accent);
      }
    }
  }

  &__right {
  }

  &__main {
    border-radius: clamp(10px, 2vw, 20px);
    background: #fdffdd;
    padding: 17px;
    display: flex;
    flex-direction: column;
    height: 100%;
    @media (max-width: 1200px) {
      padding: 12px;
    }
    .image {
      margin-bottom: 35px;
      height: 240px;
      overflow: hidden;
      border-radius: 20px;
      img {
        border-radius: 20px;
        width: 100%;
        height: 100%;
      }
    }

    .title {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 18px;
      line-height: 94%;
      letter-spacing: -0.02em;
      color: var(--gray);
      transition: color var(--transition);
      margin-bottom: 15px;
      @media (any-hover: hover) {
        &:hover {
          color: var(--accent);
        }
      }
    }

    .desc {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
      opacity: 0.5;
      flex: 1 1 auto;
      margin-bottom: 20px;
    }

    .date {
      border-radius: 100px;
      padding: 5px 9px;
      background: var(--white);
      display: flex;
      align-items: center;
      gap: 16px;

      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--accent);
      max-width: fit-content;
      .icon {
        fill: var(--accent);
      }
    }
  }
}
